.content .header-color {
  background-color: black;
  color: white;
}

.content a {
  text-decoration: none;
}

.content h3 {
  font-family: fantasy;
}

.header-color {
  position: fixed;
  font-family: fantasy;
}

.content div {
  scroll-behavior: smooth;
}

.landing-grid {
  background-image: url("./Pics/AHpic.png");

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.75;

  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}

.avatar-img {
  width: 300px;
  height: 350px;
  margin: 5em 0 2em 0;
  border-radius: 50%;
  object-fit: none;
  object-position: 49% 10%;
  border: 2px solid aqua;
}

.banner-text {
  background-color: rgba(4, 4, 17, 0.76);
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 100px;
  border: 2px solid aqua;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
  text-shadow: 3px 3px #1eaad4;
}

.banner-text hr {
  border-top: 5px double grey;
  width: 50%;
  margin: auto;
  border-bottom: 5px solid aqua;
  border-radius: 10px;
  border-right: 5px solid aqua;
  border-left: 5px solid aqua;
}

.banner-text p {
  color: white;
  font-size: 25px;
  padding: 1em;
  /*text-shadow: 2px 2px #8ac6ef;*/
}

.efficient {
  background-color: blue;
}

.proficient {
  background-color: rgb(140, 0, 255);
}

.social-links {
  display: block;
  justify-content: space-between;
  width: 50%;
  margin: auto;
  padding: 15px 0 15px 0;
}

.social-links img {
  padding: 6px;
  width: 15%;
  margin-left: 10px;
  border-radius: 80px;
  background: white;
  border: 2px solid aqua;
}

.social-links img:hover {
  background-color: aqua;
  border: aqua solid 2px;
  cursor: pointer;
  opacity: 0.5;
  border: 2px solid white;
}

.projects-grid {
  display: flex;
}

.about-me-grid {
  background: #06beb6; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #48b1bf,
    #06beb6
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #48b1bf,
    #06beb6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}

.about-me-grid h2 {
  font-size: 45px;
  font-weight: bold;
  color: white;
  margin-top: 150px;
  letter-spacing: 5px;
}

.about-me-grid hr {
  margin: auto;
  width: 35%;
}

.paragraph p {
  color: white;
  width: 50%;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;
  font-size: 18px;
  text-align: left;
  letter-spacing: 1.5px;
}

.about-me-cell {
  background-color: black;
  color: white;
  opacity: 0.8;
}

.content-grid-proj {
  width: 70%;
  margin: auto;
}

.category-tabs {
  text-align: center;
  margin-bottom: 100px;
}

.category-tabs h1,
.contact-content h1 {
  font-size: 45px;
  font-weight: bold;
  color: black;
  margin-top: 50px;
  letter-spacing: 5px;
  text-align: center;
  font-family: fantasy;
}

.category-tabs hr,
.contact-content hr {
  margin: auto;
  width: 30%;
  margin-top: -20px;
  margin-bottom: 20px;
  height: 5px;
  background-color: aqua;
}

.projects-date {
  font-size: 12px;
  opacity: 0.5;
  color: black;
  text-align: right;
  margin-left: 140px;
}

.contact-content hr {
  background-color: silver;
}

.contact-content h1 {
  color: black;
}

.contact-content {
  background-color: aqua;
}

.contact-content-cell {
  text-align: center;
  padding-top: 30px;
}

.contact-info {
  text-align: center;

  align-items: center;
  justify-content: center;
}

.contact-info img {
  margin-top: 20px;
  width: 10%;
  background: aqua;
}

.contact-info p {
  font-size: 25px;
  font-family: fantasy;
  margin-top: 20px;
  color: black;
}
